.Movie {
    background-color:white;
    padding : 0 20px;
    width: 40%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    /* flex-wrap: wrap; */
    margin-bottom: 50px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    -webkit-box-shadow: 0px 12px 15px rgba(133, 133, 133, 0.3), 15px 30px 40px rgba(133, 133, 133, 0.22);
            box-shadow: 0px 12px 15px rgba(133, 133, 133, 0.3), 15px 30px 40px rgba(133, 133, 133, 0.22);
    
}

.Movie__Columns {
    width: 30%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.Movie__Columns:last-child{
    padding: 20px 0;
    width: 60%;
}

.Movie h1 {
    font-size: 20px;
    font-weight: 600;
}

.Movie__Columns .Movie__Genres{
    display: -ms-flexbox;
    display: flex;
    /* flex-wrap: wrap; */
    margin-bottom: 20px;
}

.Movie__Genres .Movie__Genre {
    margin-right: 5px;
    color : #B4B5C5;
}

.Movie__Columns .Movie__synopsis{
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    color : #B4B5C5;
}

.Movie__Poster{
    position: relative;
    top : -20px;
    max-width: 100%;
    -webkit-box-shadow: -10px 12px 15px rgba(83, 83, 83, 0.3), 10px 12px 15px rgba(83, 83, 83, 0.22);
            box-shadow: -10px 12px 15px rgba(83, 83, 83, 0.3), 10px 12px 15px rgba(83, 83, 83, 0.22);
}

@media screen and (min-width : 320px) and (max-width : 667px){
    .Movie {
        width: 100%;
    }
}

@media screen and (min-width : 320px) and (max-width : 667px) and (orientation : portrait){
    .Movie {
        width: 100%;
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .Movie__Poster {
        width: 100%;
        top: 0;
        left: 0;
    }
    .Movie__Columns{
        width: 100%;
    }
}


